// 
import Template from "./template";

class UndParser extends Template {
    constructor(option, content, rendererData) {
        super(option, content, rendererData);
        this.name = ["und", ""]; // 
    }
    judge() {
        if (this.name.indexOf(this.dataList[0]) !== -1) {
            return true;
        } else {
            return false;
        }
    }
    analyseTemplate(content) {
        this.dataListInit(content); // dataList
        if (!this.judge()) return content; // 
        // UndParser
        let text = ""; // 
        let color = "#303133"; // 
        let size = "1"; // 

        const switchKeyValue = (key, value) => {
            switch (key) {
                case "content":
                case "":
                    text = value;
                    break;
                case "color":
                case "":
                    color = value;
                    break;
                case "size":
                case "":
                    size = value;
                    break;
            }
        };
        this.dataList.forEach((data, index) => {
            const key = data.split("=")[0];
            const left = data.indexOf("=");
            const value = data.slice(left + 1, data.length);
            switch (index) {
                case 1:
                    if (switchKeyValue(key, value)) break;
                    else text = data;
                    break;
                case 2:
                    if (switchKeyValue(key, value)) break;
                    else color = data;
                    break;
                case 3:
                    if (switchKeyValue(key, value)) break;
                    else size = data;
                    break;
            }
        });

        let undItem =
            "<p style='" +
            "border-bottom: solid " +
            size +
            "px " +
            color +
            ";width: fit-content;'>" +
            text +
            "</p>";

        return undItem; // 
    }
}

export default UndParser;
