import ComponentsParser from "./componentParser"; // 

class ClearParser extends ComponentsParser {
    constructor(component, option) {
        super(component, option);
        this.name = ["clear"];
        this.template = {
            type: "sr-clear", // vuename
            data: {
                clear:"none"
            },
        }; // 
    }
    judge() {
        // 
        if (this.name.indexOf(this.dataList[0]) !== -1) {
            this.default = true;
            return true;
        } else {
            return false;
        }
    }
    analyse() {
        this.dataList.forEach((data)=>{
            let key = data.split("=")[0];
            let value = data.split("=")[data.split("=").length - 1];
            switch(key){
                case "left":
                    if(key === value) this.template.data.clear = key;
                    break;
                case "right":
                    if(key === value) this.template.data.clear = key;
                    break;
                case "both":
                    if(key === value) this.template.data.clear = key;
                    break;
                case "none":
                    if(key === value) this.template.data.clear = key;
                    break;
                
            }
        });
        return {
            type: "success",
            msg: "",
            content: this.template,
        };
    }
}

export default ClearParser;
